import React, { Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import '../../../../Css/Dashboard/Beneficiary.css'

function Beneficiaries() {
    return (
        <Fragment>
            <h1 className='text-center text-white fs-2' style={{ paddingTop: "10rem", height: "15rem" }}>Beneficiaries of FDA India</h1>
            <div className="container mt-5 pt-5"></div>
            <nav className="navbar navbar-expand-lg text-light" style={{ backgroundColor: "rgba(21,48,71,.9)" }}>
                <div className="container-fluid">
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavDropdown" style={{ justifyContent: "center" }}>
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold text-info shadow-lg' exact='true' to="/crm/user/transfer/own-bank/beneficiaries"> BENEFICIARIES </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/within" > WITHIN FDA INDIA </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/otherbank" > OTHER BANK </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/wiretransfer" > WIRE TRANSFER</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/history" >HISTORY</NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
            <div className="container-fluid transferdashboard">
                <div className='container mt-5'>
                    <div className="container mb-3">
                        <input type='button' value={"FDA India"} className='btn btn-md ms-2 text-light' style={{ backgroundColor: "rgb(2, 48, 71)" }} />
                        <input type='button' value={"Other Banks"} className='btn btn-info btn-md ms-2 text-light' />
                    </div>
                    <div className="container d-flex" style={{ justifyContent: 'end' }}>
                        <button className='btn btn-md ms-2 text-light' style={{ backgroundColor: "rgb(2, 48, 71)" }}>+ Add Beneficiary</button>
                    </div>
                    <div className="container bene">
                        <table>
                            <thead>
                                <tr>
                                    <th>ACCOUNT NUMBER</th>
                                    <th>ACCOUNT NAME</th>
                                    <th>SHORT NAME</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td></td>
                                    <td>No Data Found</td>
                                    <td></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </Fragment >
    )
}

export default Beneficiaries
